.about {
    padding: 60px 0; }

.about__title {
    @include title;
    margin-bottom: 60px; }

.about__text {
    margin-bottom: 20px; }

.about__text + .about__title {
    margin-top: 60px; }

.mission__img {
    width: 700px;
    max-width: 100%;
    margin: 0 auto 60px; }

.smi__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: -30px -30px 0; }

.smi__link {
    width: 50%;
    padding: 30px 30px;
    text-align: center; }

.smi__img {
    width: auto;
    height: auto;
    max-width: 80%;
    max-height: 200px;
    transition: transform .2s;
    &:hover {
        transform: scale(1.07); } }

.fotorama__arr,
.fotorama__fullscreen-icon,
.fotorama--fullscreen .fotorama__fullscreen-icon {
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    opacity: .7;
    &:hover {
        opacity: 1; } }

.fotorama__arr--disabled {
    opacity: 0.2; }

